* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Fira Sans', sans-serif;    
}

body {
  background:url(./src/fon.jpg);
  background-size:cover;
  background-position:center center;
  background-attachment:fixed;
}

.App {
  align-items:center;
  flex-direction: column;
  
  flex-wrap:wrap;
}

.map-box {
  position: relative;
  align-items:center;
  display:flex;
  justify-content:center; 
  width: 1188px;
  height: 839px;  
  left: 50%;
  transform: translate(-50%, 0);
}

.map {
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
  border-radius: 20px;
}

area:focus {
  outline: 4px dashed red;
}

.popup {
  position: absolute;
  display: grid;
  grid-template-columns: 100px 290px;
  grid-gap: 10px;
  width: max-content;
  padding: 10px;
  color: white;
  border: 1px solid red;
  border-radius: 10px;
  background-color:rgba(0, 0, 0, 0.6);
  transition: all .5s ease-in-out;
  -webkit-animation: pop-swirl 0.5s ease forwards;
}

@keyframes pop-swirl {
  0% {
    transform: scale(0);
  }
  60% {
    transform: scale(1.2) rotate(5deg);
  }
  100% {
    transform: scale(1) rotate(0deg);
  }
}

.add_info {
  display: grid;
  grid-template-columns: 145px 245px;
  grid-gap: 0px;
  width: max-content;
}

h1 {
  font-size: 20px;
  margin-bottom: 10px;
}
h2 {
  font-size: 15px;
  font-weight: normal;
  margin: 0px;
  width: max-content;
}

.flag {
  margin: 20px 0px;
  position: relative;
  text-align: center;  
}

.head {
  font-size: 60px;
  position: absolute;
  left: 50%;
  transform: translate(-50%, 0);
  top: 65px;
  -webkit-text-stroke: 0.5px black;
  -webkit-text-fill-color:transparent;
  text-shadow: 2px 2px 4px white;
  
}